tent: space-evenly;\n          align-items: center;\n          width: 100%;\n        }\n\n        .text-wrapper {\n          margin-top: 40px;\n          color: $paper;\n\n          @include H4;\n        }\n      }\n\n      &__title {\n        justify-content: flex-end;\n        row-gap: 15%;\n        padding: 0 0 48px;\n      }\n    }\n\n    h1 {\n      margin-bottom: 10%;\n      text-transform: initial !important;\n      font-size: 3.812rem;\n\n      @include bagerich;\n\n      span {\n        &:first-child {\n          color: $paper;\n        }\n\n        &:last-child {\n          color: $gold;\n        }\n      }\n    }\n\n    p {\n      color: $paper;\n      font-size: $font-size-p;\n    }\n\n    ul.names {\n      list-style-type: none;\n      display: flex;\n      gap: 24px;\n      color: $paper;\n      font-size: $font-size-button;\n    }\n\n    #start-button {\n      $button-width: 193px;\n      $button-height: 70px;\n\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      width: $button-width;\n      height: $button-height;\n      min-height: $button-height;\n      color: $paper;\n      text-transform: uppercase;\n\n      &::before,\n      &::after {\n        content: '';\n        width: $button-width;\n        height: $button-height;\n        min-height: $button-height;\n        position: absolute;\n      }\n\n      &::before {\n        background: url('./images/button.svg') no-repeat center center;\n        opacity: 1;\n        transition: .4s ease;\n      }\n\n      &::after {\n        background: url('./images/button-hover.svg') no-repeat center center;\n        opacity: 0;\n        transition: .4s ease;\n      }\n\n      &:hover,\n      &:active,\n      &:focus {\n        &::before { opacity: 0; }\n\n        &::after { opacity: 1; }\n      }\n    }\n  }\n\n  &#content-screen {\n    display: none;\n    background: url('./images/main_screen.png') no-repeat center center fixed;\n    background-size: cover;\n\n    $navHeight: 170px;\n    $footerHeight: 215px;\n\n    nav {\n      display: flex;\n      justify-content: space-between;\n\n      position: fixed;\n      top: 0;\n      left: 0;\n      right: 0;\n\n      height: $navHeight;\n      min-height: $navHeight;\n      max-height: $navHeight;\n      padding: 48px 48px 56px;\n\n      .logo img {\n        width: 115px;\n        height: 56px;\n      }\n    }\n\n    main {\n      position: fixed;\n      top: $navHeight;\n      left: 0;\n      right: 0;\n      height: calc(100% - #{$navHeight} - #{$footerHeight});\n\n      h2 {\n        color: $gold;\n        text-align: center;\n\n        @include H3;\n      }\n\n      div {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        justify-content: center;\n        height: 100%;\n\n        img {\n          max-width: 90%;\n          max-height: 80%;\n        }\n      }\n    }\n\n    .footer {\n      display: flex;\n      flex-direction: column;\n      justify-content: flex-end;\n\n      position: fixed;\n      bottom: 0;\n      left: 0;\n      right: 0;\n\n      height: $footerHeight;\n      min-height: $footerHeight;\n      max-height: $footerHeight;\n      padding: 0 96px 96px;\n\n      &__title {\n        margin-bottom: 16px;\n        text-align: center;\n        color: $gold;\n\n        @include H4;\n      }\n\n      &__chapo {\n        text-align: center;\n        color: $paper;\n\n        @include text;\n      }\n\n      &__subtitles {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        gap: 24px;\n\n        p {\n          max-width: 50ch;\n          color: $paper;\n\n          @include text;\n        }\n      }\n    }\n  }\n}\n\n.bagerich {\n  @include bagerich;\n}\n\n.webgl {\n  position: fixed;\n  top: 0;\n  left: 0;\n  outline: none;\n  z-index: 1;\n}\n\n.draw-canvas {\n  position: fixed;\n  top: 0;\n  left: 0;\n  outline: none;\n  z-index: 2;\n  /* pointer-events: none; */\n}\n\n.eq-canvas {\n 